.cart-icon {
    width: 45px;
    height: 45px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
    .shopping-icon {
      position: relative;
      display: flex;
      margin-right: 2vh;
      width: 35px;
      height: 35px;
      cursor: pointer;

    }
   
    .item-count {
      position: absolute;
      font-size: 10px;
      font-weight: bold;
      bottom: 19px;
      right: 24px;
      cursor: pointer;
    }
  
  